import axios from 'axios';

const service_acct_id = "test123";
const listAllUrl = `https://434cd276-9b4e-4d8c-98b1-4c01af9a2ca2.mock.pstmn.io/console/`;

/** Vuex狀態管理
 * GET、POST、PUT、PATCH、DELETE
 * 動作： dispatch->actions->commit->mutation->state->render
 */

// initial state
const state = () => ({
  dataApiKeys: [],
  dataOAuth: [],
  checkedApiKeys: [],
  checkedOAuth: [],
})

// getters
const getters = {
  getCredentialsApiKeys: state => state.dataApiKeys,
  getCredentialsOAuth: state => state.dataOAuth,
  getCheckedApiKeys: state => state.checkedApiKeys,
  getCheckedOAuth: state => state.checkedOAuth,
}

// actions
const actions = {
  /** GET ApiKeys
   * 模擬API資料 https://sit.eyesmedia.com.tw/console/<string:service_acct_id>/list-all
   */
  dataReadApiKeys: (context) => {
    return axios.get(listAllUrl + service_acct_id + "/list-all")
      .then(res => {
        context.commit('setCredentialsApiKeys', res.data.data.apiKeys);
      })
      .catch(error => console.log(error));
  },

  /** GET oauthClient
   * 模擬API資料 https://sit.eyesmedia.com.tw/console/<string:service_acct_id>/list-all
   */
  dataReadOAuth: (context) => {
    return axios.get(listAllUrl + service_acct_id + "/list-all")
      .then(res => {
        context.commit('setCredentialsOAuth', res.data.data.oauthClient);
      })
      .catch(error => console.log(error));
  },

  /** POST ApiKeys
   * 模擬API資料 https://sit.eyesmedia.com.tw/console/apikey/<string:service_acct_id>/create
   */
  dataAddApiKeys: (context, data) => {
    return axios.post(listAllUrl + "apikey/" + service_acct_id + "/create")
      .then(() => {
        context.commit('addCredentialsApiKeys', data);
      })
      .catch(error => console.log(error));
  },

  /** DELETE ApiKeys
   * 模擬API資料 https://sit.eyesmedia.com.tw/console/apikey/delete
   */
  dataDelApiKey: (context, { id }) => {
    return axios.delete(listAllUrl + 'apikey/delete')
      .then(() => {
        context.commit('deleteCredentialsApiKeys', id);
        context.commit('setCheckedApiKeys', []);
      });
  },

  // dataDelete: (context, { id, type }) => {
  //   id.forEach(function (el) {
  //     return axios.delete(listAllUrl + type + '/' + el)
  //       .then(() => {
  //         if (type === "apiKeys") {
  //           context.commit('deleteCredentialsApiKeys', el);
  //           context.commit('setCheckedApiKeys', []);
  //         } else {
  //           context.commit('deleteCredentialsOAuth', el);
  //           context.commit('setCheckedOAuth', []);
  //         }
  //       });
  //   });
  // },


  /** PATCH ApiKeys
   * 模擬API資料 https://sit.eyesmedia.com.tw/console/apikey/<string:service_acct_id>/update
   */
  dataUpdateApiKey: (context, { apiKey, name }) => {
    console.log(apiKey, name);
    const item = context.state.dataApiKeys.find(el => {
      return el.apiKey === apiKey;
    });
    console.log(item);
    if (item === -1) return false // not find
    return axios.patch(listAllUrl + "apikey/" + service_acct_id + "/update")
      .then(() => {
        context.commit('updateCredentialsApiKeys', { apiKey, name });
      });
  }
}

// mutations
const mutations = {
  setCredentialsApiKeys(state, data) {
    state.dataApiKeys = data;
  },
  setCredentialsOAuth(state, data) {
    state.dataOAuth = data;
  },
  addCredentialsApiKeys(state, data) {
    state.dataApiKeys.push(data);
  },
  addCredentialsOAuth(state, data) {
    state.dataOAuth.push(data);
  },
  deleteCredentialsApiKeys(state, data) {
    state.dataApiKeys.forEach(function (el, index) {
      for (let i = 0; i < data.length; i++) {
        if (el.apiKey === data[i])
          state.dataApiKeys.splice(index, 1);
      }
    });
  },
  deleteCredentialsOAuth(state, data) {
    const index = state.dataOAuth.findIndex((element) => element.id === data);
    state.dataOAuth.splice(index, 1);
  },
  updateCredentialsApiKeys(state, data) {
    console.log(state, data);
    // item.content = input;
  },
  updateCheckedApiKeys(state) {
    state.checkedApiKeys = [];
  },
  updateCheckedOAuth(state) {
    state.checkedOAuth = [];
  },
  setCheckedApiKeys(state, data) {
    state.checkedApiKeys = data;
  },
  setCheckedOAuth(state, data) {
    state.checkedOAuth = data;
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
}
