<template>
  <div class="drop-display">
    <div class="drop-box" :class="isActive">
      <div class="drop-menu">
        <ul>
          <li @click="modalHandler">
            <h4>API金鑰</h4>
            <p>透過一組簡單API金鑰識別專案，以便查看配額與存取權限</p>
          </li>
          <li>
            <h4>OAuth 2.0 用戶端ID</h4>
            <p>要求使用者同意您的應用程式存取其資料</p>
          </li>
        </ul>
      </div>
    </div>
    <ModalCredentialsCreate v-model:openModal="openModal" v-if="openModal" />
  </div>
</template>
<script>
import ModalCredentialsCreate from "@/components/Credentials/ModalCredentialsCreate";
export default {
  name: "DropCredentialsCreate",
  components: {
    ModalCredentialsCreate,
  },
  props: {
    isOpen: Boolean,
  },
  data() {
    return {
      openModal: false,
    };
  },
  computed: {
    isActive() {  
      // 初始下拉選單    
      return this.isOpen === true ? "active" : "hidden";
    },
  },
  methods: {
    modalHandler() {
      // 開啟modal
      this.openModal = true;

      // 關閉下拉選單, 並回傳isOpen
      this.$emit("update:isOpen", false);
      if (this.isOpen === false && this.openModal === false)
        this.isActive = "hidden";
    },
  },
};
</script>

