<template>
  <transition name="fade">
    <div class="modal">
      <div class="modal__dialog">
        <button type="button" class="btn btn__close" @click="closeModal">
          <i class="fa fa-times" aria-hidden="true"></i>
        </button>
        <div>
          <div class="modal__header">
            <h4>刪除憑證</h4>
          </div>
          <div class="modal__body">
            <div class="modal-content">
              <p>
                這個憑證將會立即被刪除，而且無法再復原。憑證刪除後，就無法再用於發出
                API 要求。
              </p>
              <p>確定要刪除這{{ deleteItems }}個憑證嗎？</p>
            </div>
          </div>
          <div class="modal__footer">
            <div class="footer-button-box">
              <button class="btn clear-btn" @click="closeModal">取消</button>
              <button class="btn default-btn" @click="done">刪除</button>
            </div>
          </div>
        </div>
      </div>
      <div class="modal__backdrop" @click="closeModal"></div>
    </div>
  </transition>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "ModalCredentialsDelete",
  props: {
    modalOption: {
      openModal: Boolean,
      apiId: Array,
    },
    type: String,
  },
  data() {
    return {};
  },
  computed: {
    // get data
    ...mapGetters("credentials", {
      checkedApiKeys: "getCheckedApiKeys",
      checkedOAuth: "getCheckedOAuth",
    }),
    deleteItems() {
      return this.checkedApiKeys.length + this.checkedOAuth.length;
    }
  },
  methods: {
    ...mapMutations("credentials", ["setCheckedApiKeys", "setCheckedOAuth"]),
    closeModal() {
      this.$emit("update:modalOption", { openModal: false, apiId: [] });
      this.setCheckedApiKeys([]);
      this.setCheckedOAuth([]);
      this.$router.push({ name: "Credentials" });
    },
    done() {
      let id = this.modalOption.apiId;
      if(this.checkedApiKeys.length > 0) {
        id = this.checkedApiKeys;
        this.$store.dispatch("credentials/dataDelApiKey", { id });
      }
      if(this.checkedOAuth.length > 0) {
        id = this.checkedOAuth;
        this.$store.dispatch("credentials/dataDelOAuth", { id });
      }
      this.$emit("update:modalOption", { openModal: false, apiId: [] });
      this.$router.push({ name: "Credentials" });
    },
  },
};
</script>
