<template>
  <div class="flex-center">
    <div class="tooltip" @click="open">
      <span class="tooltiptext">導覽選單</span>
      <i class="fa fa-bars fa-lg" aria-hidden="true"></i>
    </div>
    <p v-once>{{ msg }}</p>
  </div>
</template>

<script>
export default {
  name: "HeadLogo",
  props: {
    msg: String,
  },
  data() {
    return {
      isOpen: this.isOpen,
    };
  },
  methods: {
    open() {
      this.isOpen = !this.isOpen;
      this.$emit("is-open", this.isOpen);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/styles/vars";
i {
  padding: 0.5em;
  border-radius: 50%;
  &:hover {
    background: rgba(255, 255, 255, 0.29);
  }
}
</style>
