<template>
  <!-- 憑證標頭 -->
  <div v-if="titleShow === 'credentialsDefaultHead'">
    <div class="flexfit">
      <div class="flex-item">
        <h4>憑證</h4>
      </div>
      <div class="flex-item">
        <button class="btn white" @click="clickHandler">
          <i class="fa fa-plus" aria-hidden="true"></i>
          <span>建立憑證</span>
        </button>
        <DropCredentialsCreate v-model:isOpen="isOpen" />
      </div>
      <div class="flex-item">
        <div class="tooltip">
          <button
            class="btn white"
            @click="deleteHandler"
            :disabled="deleteItems === 0"
          >
            <i class="fa fa-trash" aria-hidden="true"></i>
            <span>刪除</span>
          </button>
          <span class="tooltiptext" v-show="deleteItems === 0"
            >必須至少選取一個憑證</span
          >
        </div>
      </div>
      <ModalCredentialsDelete
        v-model:modalOption="modalOption"
        v-if="modalOption.openModal"
      />
    </div>
  </div>
  <!-- 憑證修改標頭 -->
  <div v-else-if="titleShow === 'credentialsUpdateHead'">
    <div class="flexfit">
      <div class="flex-item">
        <button
          class="btn white"
          @click="$router.push({ name: 'Credentials' })"
        >
          <i class="fa fa-arrow-left" aria-hidden="true"></i>
        </button>
      </div>
      <div class="flex-item">
        <h4>限制並重新命名API金鑰</h4>
      </div>
      <div class="flex-item">
        <button class="btn white" @click="clickHandler">
          <i class="fa fa-plus" aria-hidden="true"></i>
          <span>重新產生金鑰</span>
        </button>
        <DropCredentialsCreate v-model:isOpen="isOpen" />
      </div>
      <div class="flex-item">
        <div class="tooltip">
          <button class="btn white">
            <i class="fa fa-trash" aria-hidden="true"></i>
            <span>刪除</span>
          </button>
          <span class="tooltiptext">必須至少選取一個憑證</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import DropCredentialsCreate from "@/components/Credentials/DropCredentialsCreate";
import ModalCredentialsDelete from "@/components/Credentials/ModalCredentialsDelete";
export default {
  name: "HeadTitle",
  components: {
    DropCredentialsCreate,
    ModalCredentialsDelete,
  },
  data() {
    return {
      isOpen: false,
      modalOption: {
        openModal: false,
        apiId: [],
      },
      deleteItems: 0
    };
  },
  computed: {
    // get data
    ...mapGetters("credentials", {
      checkedApiKeys: "getCheckedApiKeys",
      checkedOAuth: "getCheckedOAuth",
    }),
    titleShow() {
      if (
        this.$route.path.indexOf("credentialsUpdate") === 1 &&
        this.$router.currentRoute.value.params.id > 0 === true
      ) {
        return "credentialsUpdateHead";
      } else if (this.$route.path.indexOf("credentials") === 1) {
        return "credentialsDefaultHead";
      } else {
        return null;
      }
    },
  },
  methods: {
    ...mapMutations("credentials", ["setCheckedApiKeys", "setCheckedOAuth"]),
    clickHandler() {
      this.isOpen = !this.isOpen;
      this.setCheckedApiKeys([]);
      this.setCheckedOAuth([]);
    },
    deleteHandler() {
      this.modalOption.openModal = true;
    },
    countDeleteItem() {
      this.deleteItems = this.checkedApiKeys.length + this.checkedOAuth.length;
    }
  },
  watch: {
    checkedApiKeys(newValue) {
      this.setCheckedApiKeys(newValue);
      this.countDeleteItem();
    },
    checkedOAuth(newValue) {
      this.setCheckedOAuth(newValue);
      this.countDeleteItem();
    },
  },
};
</script>

