import { createRouter, createWebHistory } from 'vue-router'
import Credentials from '../views/Credentials.vue'
import CredentialsUpdateApiKey from '../views/CredentialsUpdateApiKey.vue'
import CredentialsUpdateOAuth from '../views/CredentialsUpdateOAuth.vue'

import Home from '@/views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/credentials',
    name: 'Credentials',
    component: Credentials,
  },
  {
    path: '/credentialsUpdate/:apiKey',
    name: 'CredentialsUpdateApiKey',
    component: CredentialsUpdateApiKey
  },
  {
    path: '/credentialsUpdate/:id',
    name: 'CredentialsUpdateOAuth',
    component: CredentialsUpdateOAuth
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
