<template>
  <div>
    <h4>{{ credentialsTitle }}</h4>
    <table>
      <thead>
        <tr>
          <th v-for="(column, name, index) in thName" :key="column">
            <input type="checkbox" v-model="checkedAllOAuth" v-if="index === 0" />
            <span @click="sortBy(name)">{{ column }}</span>
            <button
              @click="sortBy(name)"
              :class="[
                'btn small',
                { active: currentSort === name },
                currentSortDir,
              ]"
            >
              <i
                :class="[
                  'fa',
                  { 'fa-long-arrow-up': currentSortDir === 'asc' },
                  { 'fa-long-arrow-down': currentSortDir === 'desc' },
                ]"
                aria-hidden="true"
              ></i>
            </button>
          </th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="items in oauthClientList" :key="items.id">
          <td>
            <input
              type="checkbox"
              v-model="checkedItemOAuth"
              :value="items.id"
            />
            <span>{{ items.name }}</span>
          </td>
          <td>{{ items.clientCrtDate }}</td>
          <td>
            <div class="flexfrm key-box">
              <span>{{ items.id }}</span>
              <div class="fun-bar">
                <button class="btn" @click="updateHandler(items.id)">
                  <i class="fa fa-pencil" aria-hidden="true"></i>
                </button>
                <button class="btn" @click="deleteHandler(items.id)">
                  <i class="fa fa-trash" aria-hidden="true"></i>
                </button>
                <button class="btn" @click="downloadHandler(items.id)">
                  <i class="fa fa-download" aria-hidden="true"></i>
                </button>
              </div>
            </div>
          </td>
        </tr>
        <tr v-show="oauthClientListLength === 0">
          <td colspan="3">沒有可顯示的{{ credentialsTitle }}</td>
        </tr>
      </tbody>
    </table>
  </div>
  <ModalCredentialsDelete
    type="oauthClient"
    v-model:modalOption="modalOption"
    v-if="modalOption.openModal"
  />
</template>

<script>
import { ref } from "vue";
import { mapGetters, mapActions, mapMutations } from "vuex";
import ModalCredentialsDelete from "@/components/Credentials/ModalCredentialsDelete";
export default {
  name: "CredentialsApiKeys",
  components: {
    ModalCredentialsDelete,
  },
  setup() {
    /** 憑證標頭 */
    const credentialsTitle = ref("OAuth 2.0 用戶端 ID");
    /** 個別選取 */
    const checkedItemOAuth = ref([]);
    /** 排序預設欄位 */
    const currentSort = ref("clientCrtDate");
    /** 排序方式desc(由大至小)；asc(由小至大) */
    const currentSortDir = ref("desc");
    /** 表格標頭 */
    const thName = ref({
      name: "名稱",
      clientCrtDate: "建立日期",
      id: "ID",
    });
    /** 初始化modal */
    const modalOption = ref({
      openModal: false,
      apiId: [],
    });

    return {
      credentialsTitle,
      modalOption,
      currentSort,
      currentSortDir,
      thName,
      checkedItemOAuth,
    };
  },
  computed: {
    /** Vuex轉出資料(getters->state) */
    ...mapGetters("credentials", {
      credentialsData: "getCredentialsOAuth",
      checkedOAuth: "getCheckedOAuth"
    }),
    /** 取得排序後資料 */
    oauthClientList() {
      this.sort();
      return this.credentialsData;
    },
    /** 資料長度 */
    oauthClientListLength() {
      return this.credentialsData !== undefined
        ? this.credentialsData.length
        : 0;
    },
    /** 全選 */
    checkedAllOAuth: {
      get: function() {
        return this.oauthClientList
          ? this.checkedItemOAuth.length === this.oauthClientList.length
          : false;
      },
      set: function(value) {
        const selected = [];
        if (value) {
          this.oauthClientList.forEach((checked) => {
            selected.push(checked.id);
          });
        }
        this.checkedItemOAuth = selected;
      },
    },
  },
  watch: {
    checkedItemOAuth(newValue) {
      /** 設定已選取 */
      this.setCheckedOAuth(newValue);
    },
    checkedOAuth(newValue){
      /** 清除已選取 */
      if(newValue.length === 0 && this.checkedOAuth.length === 0) {
        this.checkedItemOAuth.forEach((checked) => {
          if(checked >= 0)
          this.checkedItemOAuth = [];
        });
      }
    },
  },
  methods: {
    /** Vuex轉出資料(actions->mutations->state) */
    ...mapActions("credentials", {
      dataReadOAuth: "dataReadOAuth",
    }),
    /** Vuex變更State(mutations->state) */
    ...mapMutations("credentials", [
      "setCheckedOAuth",
      "updateCheckedOAuth",
    ]),
    /** 排序 */
    sort() {
      return Object.assign(this.credentialsData).sort((a, b) => {
        let modifier = 1;
        if (this.currentSortDir === "asc") modifier = 1;
        if (this.currentSortDir === "desc") modifier = -1;
        if (a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if (a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },
    /** 點擊排序
     * 欄位排序預設desc，當切換欄位排序預設asc
     * @param column 排序欄位名稱
     */
    sortBy(column) {
      if (this.currentSort === column) {
        this.currentSortDir = this.currentSortDir === "asc" ? "desc" : "asc";
      } else {
        this.currentSortDir = "asc";
      }
      this.currentSort = column;
    },
    /** Vuex變更API(GET:actions->mutations->state) */
    updateHandler(id) {
      this.$router.push({ name: "CredentialsUpdateOAuth", params: { id: id } });
    },
    deleteHandler(id) {
      // 開啟modal
      this.checkedItemOAuth = [];
      this.checkedItemOAuth.push(id);
      this.modalOption.apiId = this.checkedItemOAuth;
      this.modalOption.openModal = true;
    }
  },
  /** Vuex讀取API暫存state(GET:actions->mutations->state) */
  mounted() {
    this.dataReadOAuth();
  },
};
</script>

<style lang="scss" scoped>
table {
  margin-bottom: 3em;
  .active {
    display: inline-block;
  }
  .btn {
    padding: 0;
    margin: 0;
    i {
      padding: 0.25rem;
      &.fa-long-arrow-down {
        font-size: small;
      }
    }
  }
  .fun-bar {
    align-self: flex-start;
    white-space: nowrap;
  }
  .key-box {
    justify-content: space-between;
    flex-direction: row;
    align-items: stretch;
    @media screen and (max-width: 767px) {
      justify-content: none;
      flex-direction: column;
    }
  }
}
</style>
