<template>
  <div>
    <h4>{{ credentialsTitle }}</h4>
    <table>
      <thead>
        <tr>
          <th v-for="(column, name, index) in thName" :key="column">
            <input type="checkbox" v-model="checkedAll" v-if="index === 0" />
            <span @click="sortBy(name)">{{ column }}</span>
            <button
              @click="sortBy(name)"
              :class="[
                'btn small',
                { active: currentSort === name },
                currentSortDir,
              ]"
            >
              <i
                :class="[
                  'fa',
                  { 'fa-long-arrow-up': currentSortDir === 'asc' },
                  { 'fa-long-arrow-down': currentSortDir === 'desc' },
                ]"
                aria-hidden="true"
              ></i>
            </button>
          </th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="items in apiKeysList" :key="items.apiKey">
          <td>
            <input
              type="checkbox"
              v-model="checkedItem"
              :value="items.apiKey"
            />
            <span>{{ items.name }}</span>
          </td>
          <td>{{ items.apiKeyCrtDate }}</td>
          <td>
            <div class="flexfrm key-box">
              <span>{{ items.apiKey }}</span>
              <div class="fun-bar">
                <button class="btn" @click="updateHandler(items.apiKey)">
                  <i class="fa fa-pencil" aria-hidden="true"></i>
                </button>
                <button class="btn" @click="deleteHandler(items.apiKey)">
                  <i class="fa fa-trash" aria-hidden="true"></i>
                </button>
              </div>
            </div>
          </td>
        </tr>
        <tr v-show="apiKeysListLength === 0">
          <td colspan="3">沒有可顯示的{{ credentialsTitle }}</td>
        </tr>
      </tbody>
    </table>
  </div>
  <ModalCredentialsDelete
    type="apiKeys"
    v-model:modalOption="modalOption"
    v-if="modalOption.openModal"
  />
</template>

<script>
import { ref } from "vue";
import { mapGetters, mapActions, mapMutations } from "vuex";
import ModalCredentialsDelete from "@/components/Credentials/ModalCredentialsDelete";
export default {
  name: "CredentialsApiKeys",
  components: {
    ModalCredentialsDelete,
  },
  setup() {
    /** 憑證標頭 */
    const credentialsTitle = ref("API金鑰");
    /** 個別選取 */
    const checkedItem = ref([]);
    /** 排序預設欄位 */
    const currentSort = ref("apiKeyCrtDate");
    /** 排序方式desc(由大至小)；asc(由小至大) */
    const currentSortDir = ref("desc");
    /** 表格標頭 */
    const thName = ref({
      name: "名稱",
      apiKeyCrtDate: "建立日期",
      apiKey: "key",
    });
    /** 初始化modal */
    const modalOption = ref({
      openModal: false,
      apiId: [],
    });

    return {
      credentialsTitle,
      modalOption,
      currentSort,
      currentSortDir,
      thName,
      checkedItem,
    };
  },
  computed: {
    /** Vuex轉出資料(getters->state) */
    ...mapGetters("credentials", {
      credentialsData: "getCredentialsApiKeys",
      checkedApiKeys: "getCheckedApiKeys"
    }),
    /** 取得排序後資料 */
    apiKeysList() {
      this.sort();
      return this.credentialsData;
    },
    /** 資料長度 */
    apiKeysListLength() {
      return this.credentialsData !== undefined
        ? this.credentialsData.length
        : 0;
    },
    /** 全選 */
    checkedAll: {
      get: function() {
        return this.apiKeysList
          ? this.checkedItem.length === this.apiKeysList.length
          : false;
      },
      set: function(value) {
        const selected = [];
        if (value) {
          this.apiKeysList.forEach((checked) => {
            selected.push(checked.apiKey);
          });
        }
        this.checkedItem = selected;
      },
    },
  },
  watch: {
    checkedItem(newValue) {
      /** 設定已選取 */
      this.setCheckedApiKeys(newValue);
    },
    checkedApiKeys(newValue){
      /** 清除已選取 */
      if(newValue.length === 0 && this.checkedApiKeys.length === 0) {
        this.checkedItem.forEach((checked) => {
          if(checked >= 0)
          this.checkedItem = [];
        });
      }
    }
  },
  methods: {
    /** Vuex轉出資料(actions->mutations->state) */
    ...mapActions("credentials", {
      dataReadApiKeys: "dataReadApiKeys",
    }),
    /** Vuex變更State(mutations->state) */
    ...mapMutations("credentials", [
      "setCheckedApiKeys",
      "updateCheckedApiKeys",
    ]),
    /** 排序 */
    sort() {
      return Object.assign(this.credentialsData).sort((a, b) => {
        let modifier = 1;
        if (this.currentSortDir === "asc") modifier = 1;
        if (this.currentSortDir === "desc") modifier = -1;
        if (a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
        if (a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
        return 0;
      });
    },
    /** 點擊排序
     * 欄位排序預設desc，當切換欄位排序預設asc
     * @param column 排序欄位名稱
     */
    sortBy(column) {
      if (this.currentSort === column) {
        this.currentSortDir = this.currentSortDir === "asc" ? "desc" : "asc";
      } else {
        this.currentSortDir = "asc";
      }
      this.currentSort = column;
    },
    /** Vuex變更API(GET:actions->mutations->state) */
    updateHandler(id) {
      this.$router.push({ name: "CredentialsUpdateApiKey", params: { apiKey: id } });
    },
    /** 點擊刪除，傳入欲刪除筆數，並開啟modal再次確認是否刪除 */
    deleteHandler(id) {
      this.checkedItem = [];
      this.checkedItem.push(id);
      this.modalOption.apiId = this.checkedItem;
      this.modalOption.openModal = true;
    }
  },
  /** Vuex讀取API暫存state(GET:actions->mutations->state) */
  mounted() {
    this.dataReadApiKeys();
  },
};
</script>

<style lang="scss" scoped>
table {
  margin-bottom: 3em;
  .active {
    display: inline-block;
  }
  .btn {
    padding: 0;
    margin: 0;
    i {
      padding: 0.25rem;
      &.fa-long-arrow-down {
        font-size: small;
      }
    }
  }
  .fun-bar {
    align-self: flex-start;
    white-space: nowrap;
  }
  .key-box {
    justify-content: space-between;
    flex-direction: row;
    align-items: stretch;
    @media screen and (max-width: 767px) {
      justify-content: none;
      flex-direction: column;
    }
  }
}
</style>
