<template>
  <div class="flexfrm">
    <CredentialsAside />
    <div v-if="content === undefined" class="flex-item main-content">
      <div class="api-warning">
        <i class="fa fa-exclamation-triangle" aria-hidden="true"></i>
        <p>
          查不到資料，請重新操作！
        </p>
      </div>
    </div>
    <div v-else class="flex-item main-content">
      <form ref="formApiKey">
        <div class="content-row">
          <div class="col-6 col-m12 float-right">
            <fieldset>
              <legend>API Key</legend>
              <input type="text" name="apiKey" v-model.trim="apiKey" disabled />
            </fieldset>
            <ul class="api-create-info">
              <li>
                <div>
                  如要在應用程式中使用這組金鑰，請以 key=API_KEY
                  參數的形式來傳送金鑰。
                </div>
              </li>
              <li>
                <div class="item-title">建立日期</div>
                <div class="item-content">{{ crt_date }}</div>
              </li>
              <li>
                <div class="item-title">建立者</div>
                <div class="item-content">mei.wu@eyesmedia.com.tw (您本人)</div>
              </li>
            </ul>
          </div>
          <div class="col-6 col-m12">
            <fieldset>
              <legend>名稱*</legend>
              <input
                type="text"
                name="apiName"
                minlength="1"
                maxlength="50"
                :value="content.name"
                @keyup="btKeyUp"
              />
            </fieldset>
            <h5>金鑰限制</h5>
            <div class="api-warning">
              <i class="fa fa-exclamation-triangle" aria-hidden="true"></i>
              <p>
                這個金鑰沒有受到限制。設定限制有助於避免未授權的使用行為和配額遭到竊用。
                <router-link to="/">
                  瞭解詳情<i class="fa fa-external-link" aria-hidden="true"></i>
                </router-link>
              </p>
            </div>
            <h5>應用程式限制</h5>
            <div>
              <p>
                有了應用程式限制功能，您就能控管哪些網站、IP
                位址或應用程式可以存取您的 API
                金鑰。請注意，一組金鑰只能設定一項應用程式限制。
              </p>
              <div id="v-model-appRestrictions" class="radio-box">
                <input
                  type="radio"
                  id="appRestrictions1"
                  value="1"
                  v-model="appRestrictionsPicked"
                />
                <label for="appRestrictions1">無</label><br />
                <input
                  type="radio"
                  id="appRestrictions2"
                  value="2"
                  v-model="appRestrictionsPicked"
                />
                <label for="appRestrictions2">HTTP 參照網址 (網站)</label><br />
                <input
                  type="radio"
                  id="appRestrictions3"
                  value="3"
                  v-model="appRestrictionsPicked"
                />
                <label for="appRestrictions3"
                  >IP 位址 (網路伺服器和 Cron 工作等)</label
                ><br />
                <input
                  type="radio"
                  id="appRestrictions4"
                  value="4"
                  v-model="appRestrictionsPicked"
                />
                <label for="appRestrictions4">Android 應用程式</label><br />
                <input
                  type="radio"
                  id="appRestrictions5"
                  value="5"
                  v-model="appRestrictionsPicked"
                />
                <label for="appRestrictions5">iOS 應用程式</label>
              </div>
            </div>
            <h5>API 限制</h5>
            <div>
              <p>
                API 限制會指定這個金鑰可呼叫的已啟用 API
              </p>
              <div id="v-model-apiRestrictions" class="radio-box">
                <input
                  type="radio"
                  id="apiRestrictions1"
                  value="1"
                  v-model="apiRestrictionsPicked"
                />
                <label for="apiRestrictions1"
                  >不限制金鑰<br />
                  <div class="lable-memo">這組金鑰可呼叫任何 API</div></label
                >
                <input
                  type="radio"
                  id="apiRestrictions2"
                  value="2"
                  v-model="apiRestrictionsPicked"
                />
                <label for="apiRestrictions2">限制金鑰</label>
              </div>
              <p>注意事項：設定最多可能需要 5 分鐘才會生效</p>
              <div>
                <button class="btn default-bg-btn" @click="updateHandler">儲存</button>
                <button class="btn clear-btn" @click="clearHandler">取消</button>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import CredentialsAside from "@/components/Credentials/CredentialsAside";
export default {
  name: "CredentialsUpdate",
  data() {
    return {
      appRestrictionsPicked: 1,
      apiRestrictionsPicked: 1,
      apiName: '',
    };
  },
  components: {
    CredentialsAside,
  },
  computed: {
    /** Vuex轉出資料(getters->state) */
    ...mapGetters("credentials", {
      credentialsData: "getCredentialsApiKeys",
    }),
    content() {
      return this.credentialsData.filter((item) => {
        return item.apiKey === this.$route.params.apiKey;
      })[0];
    },
    apiKey() {
      return this.content !== undefined ? this.content.apiKey : "";
    },
    crt_date() {
      return this.content !== undefined ? this.content.crt_date : "";
    },
  },
  methods: {
    /** Vuex轉出資料(actions->mutations->state) */
    ...mapActions("credentials", {
      dataReadApiKeys: "dataReadApiKeys",
    }),
    clearHandler() {
      this.$router.push({ name: "Credentials" });
    },
    updateHandler() {
      this.$store.dispatch('credentials/dataUpdateApiKey', {
        apiKey: this.$route.params.apiKey,
        name: this.$refs.formApiKey.apiName.value
      });
    },
    btKeyUp(e) {
      /** 限制輸入特殊字元 */
      // eslint-disable-next-line no-useless-escape
      e.target.value = e.target.value.replace(/[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]·~！@#￥%……&*（）——\-+={}|《》？：“”【】、；‘’，。、]/g, '');
    }
  },
  /** Vuex讀取API暫存state(GET:actions->mutations->state) */
  mounted() {
    this.dataReadApiKeys();
  },
};
</script>

