<template>
  <div class="wrap">
    <header class="header-top">
      <div class="container flexfrm">
        <div class="flex-item-aside">
          <HeadLogo msg="API和服務" @is-open="updateOpen" />
        </div>
        <div class="flex-item">
          <HeadTitle />
        </div>
      </div>
    </header>
    <div class="scroller">
      <aside :class="[isOpen ? 'is-open' : 'is-close']" v-show="isFirst">
        <div class="menu">
          <AsideMenu @is-open="updateOpen" />
        </div>
        <div class="page-overlay" @click="open" />
      </aside>
      <main>
        <div class="container">
          <router-view />
        </div>
      </main>
    </div>
  </div>
</template>

<script>
import HeadLogo from "@/components/HeadLogo";
import HeadTitle from "@/components/HeadTitle";
import AsideMenu from "@/components/AsideMenu";

export default {
  name: "Home",
  components: {
    HeadLogo,
    HeadTitle,
    AsideMenu,
  },
  data() {
    return {
      isOpen: false,
      isFirst: 0,
    };
  },
  methods: {
    open() {
      this.isOpen = !this.isOpen;
      this.$emit("is-open", this.isOpen);
    },
    updateOpen(id) {
      this.isFirst = 1;
      this.isOpen = id;
    },
  },
};
</script>

