<template>
  <transition name="fade">
    <div class="modal">
      <div class="modal__dialog">
        <button type="button" class="btn btn__close" @click="closeModal">
          <i class="fa fa-times" aria-hidden="true"></i>
        </button>
        <div ref="s0" v-if="atIndex(0)">
          <form>
            <div class="modal__header">
              <h4>請輸入API金鑰名稱</h4>
            </div>
            <div class="modal__body">
              <div class="modal-content">
                <fieldset>
                  <legend>API金鑰名稱</legend>
                  <input
                    type="text"
                    name="apiName"
                    v-model.trim="apiName"
                    placeholder="請輸入API金鑰名稱"
                    minlength="1"
                    maxlength="50"
                  />
                  <small
                    class="small-warning"
                    v-show="attemptSubmit && apiName"
                  >
                    <i class="fa fa-exclamation-triangle" aria-hidden="true"></i
                    >API金鑰名稱為必填
                  </small>
                </fieldset>
              </div>
            </div>
            <div class="modal__footer">
              <button
                class="btn default-btn"
                type="submit"
                @click.prevent="next"
              >
                新增
              </button>
            </div>
          </form>
        </div>

        <div ref="s1" v-if="atIndex(1)">
          <div class="modal__header">
            <h4>建立的API金鑰</h4>
          </div>
          <div class="modal__body">
            <div class="modal-content">
              <p>
                如要在應用程式中使用這組金鑰，請以 key=API_KEY
                參數的形式來傳送金鑰
              </p>
              <fieldset>
                <legend>您的API金鑰</legend>
                <input
                  type="text"
                  name="apiKey"
                  v-model="apiKey"
                  :vlaue="apiKey"
                />
              </fieldset>
            </div>
          </div>
          <div class="modal__footer">
            <button class="btn clear-btn" @click="done">關閉</button>
          </div>
        </div>
      </div>

      <div class="modal__backdrop"></div>
    </div>
  </transition>
</template>

<script>
import { ref, computed } from "vue";
import { useStore } from 'vuex'
export default {
  name: "ModalCredentialsCreate",
  namespaced: true,
  props: {
    openModal: Boolean,
    closeCallback: Function,
  },
  components: {},
  setup(props, { emit }) {
    /** 憑證名稱 */
    const apiName = ref("");
    /** 步驟索引初始 */
    const index = ref(0);
    /** 憑證名稱錯誤提示初始 */
    const attemptSubmit = ref(false);

    /** 日期格式YYYY-MM-DD HH:MM:SS */
    const formatedTimestamp = computed(() => {
      const d = new Date();
      const date = d.toISOString().split("T")[0];
      const time = d.toTimeString().split(" ")[0];
      return `${date} ${time}`;
    });

    /** 亂數產生KEY */
    const apiKey = computed(() => {
      const chars =
        "0123456789ABCDEFGHIJKLMNOPQRSTUVWXTZabcdefghiklmnopqrstuvwxyz";
      const string_length = 16;
      let randomstring = "";
      for (var i = 0; i < string_length; i++) {
        var rnum = Math.floor(Math.random() * chars.length);
        randomstring += chars.substring(rnum, rnum + 1);
      }
      return randomstring;
    });

    /** 關閉modal */
    const closeModal = () => {
      emit("update:openModal", false);
    };

    /** 步驟切換--頁索引 */
    function atIndex(index, round) {
      var current = this.index;
      if (typeof round !== "undefined") {
        current = Math.floor(current);
      }
      return current == index;
    }

    /** 步驟切換--下一步 */
    function next() {
      if (this.apiName === "") {
        this.attemptSubmit = true;
        return;
      }
      this.index += 0.5;
      var self = this;
      setTimeout(function() {
        self.index += 0.5;
      }, 600);
    }

    /** 步驟完成--儲存API(POST:actions->mutations->state) */
    // () => ({
    //   ...mapActions("credentials", ["dataAddApiKeys"]),
    // });
    const store = useStore();
    
    function done() {
      this.closeModal();
      store.dispatch('credentials/dataAddApiKeys', {
        name: this.apiName,
        apiKeyCrtDate: this.formatedTimestamp,
        apiKey: this.apiKey,      
      });
      this.apiName === "";
    }

    return {
      apiName,
      index,
      attemptSubmit,
      formatedTimestamp,
      apiKey,
      closeModal,
      atIndex,
      next,
      done,
    };
  },
  // methods: {
  //   ...mapActions("credentials", {
  //     done: "dataAddApiKeys",
  //   }),
  // },
  // mounted() {
  //   this.dataReadApiKeys();
  // },
};
</script>
